* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo img {
  align-items: center;
  width: 40px;
  height: 40px;
  display: flex;
}

.nav__list {
  align-items: center;
  gap: 20px;
  height: 60px;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  padding: 23px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
  display: flex;
  position: relative;
}

.is-active {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 37px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:hover {
  color: #00acdc;
  transition: all .3s;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 16px;
  transition: all .3s;
  display: flex;
}

.card img {
  width: 160px;
  height: 134px;
  margin-top: 16px;
}

.card__content {
  flex-direction: column;
  display: flex;
}

.card__title {
  color: #060b35;
  margin-top: 40px;
  line-height: 18px;
}

.card__title p:first-child {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title p:last-child {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  color: #060b35;
  justify-content: space-between;
  align-items: end;
  margin-top: 16px;
  margin-bottom: 16px;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__rating .stars {
  justify-content: center;
  align-items: center;
  display: flex;
}

.card__rating .stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 15px;
  height: 15px;
  margin-right: 4px;
  line-height: 14px;
}

.card__rating .stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card__price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 8px;
  margin-bottom: 16px;
  display: flex;
}

.card__price p:first-child {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.card__price p:last-child {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 4px;
  width: 166px;
  height: 40px;
  padding: 12px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  scale: 120%;
}

.card:hover .card__title p:first-child {
  color: #34568b;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media screen and (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media screen and (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media screen and (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}
